body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
table,
dl,
dd {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 16px;
}

a {
  text-decoration: none;
  color: #000;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: top;
  border: none;
}

body {
  font-family: "Bai Jamjuree", "Noto Sans KR", sans-serif;
  word-break: keep-all;
  overflow-x: hidden;
}

body.stop-scrolling {
  height: 100%;
  overflow: hidden;
}

input[type=text] {
  padding: 0 10px 0 20px;
  height: 50px;
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 0.875em;
  font-weight: 600;
  border: transparent;
  background-color: rgba(234, 234, 234, 0.9);
  border-radius: 5px;
  box-sizing: border-box;
}
input[type=text]:focus {
  outline: 2px solid #888;
}

input[type=submit] {
  height: 35px;
  line-height: 35px;
  font-family: "Noto Sans KR", sans-serif;
  background-color: #000;
  color: #fff;
  border: none;
  cursor: pointer;
  padding: 0 30px;
}

textarea {
  padding: 10px 20px;
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 0.875em;
  font-weight: 600;
  border: transparent;
  background-color: rgba(234, 234, 234, 0.9);
  border-radius: 5px;
  box-sizing: border-box;
}
textarea:focus {
  outline: 2px solid #888;
}

::-webkit-input-placeholder,
:-ms-input-placeholder,
::placeholder {
  color: #444;
}

.top_btn {
  display: none;
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 70px;
  background: #000;
  text-align: center;
  z-index: 1000;
  cursor: pointer;
}
.top_btn a {
  color: #fff;
  line-height: 70px;
  font-weight: 600;
}

#main {
  height: 100vh;
  font-size: 1em;
  position: relative;
}
#main header {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  background-color: rgba(153, 153, 153, 0.6);
  z-index: 10000;
}
#main header .inner {
  max-width: 940px;
  height: 80px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
#main header.on {
  background-color: rgba(153, 153, 153, 0);
}
#main header h1 a {
  display: block;
  width: 6.875em;
  height: 1.375em;
  text-indent: -9999px;
  overflow: hidden;
  background: url(./images/main_logo.png);
  background-size: cover;
}
#main .keytext {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  mix-blend-mode: exclusion;
}
#main .keytext p {
  text-align: center;
  color: #fff;
}
#main .keytext p.title {
  font-size: 5em;
  font-weight: 700;
  line-height: 1;
}
#main .keytext p.subtitle {
  padding-top: 30px;
  font-size: 1.875em;
}
#main .bg {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  background-color: #000;
}
#main .bg video {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

#desktop ul {
  display: flex;
  justify-content: space-between;
}
#desktop ul li a {
  display: block;
  line-height: 80px;
  padding: 0 10px;
  color: #fff;
  font-size: 1.125em;
  font-weight: 500;
  position: relative;
}
#desktop ul li a::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 0;
  height: 3px;
  border-radius: 1px;
  background-color: #F5FF00;
  transition: width 0.3s 0s;
}
#desktop ul li:hover a {
  color: #F5FF00;
}
#desktop ul li.on a::after {
  width: 100%;
}

#mobile {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  width: 80%;
  height: 100vh;
  background-color: #000;
  transition: ease-in-out 0.2s;
}
#mobile ul {
  padding: 60px 30px;
}
#mobile ul li a {
  line-height: 50px;
  font-weight: 700;
  font-size: 1.5em;
  color: #fff;
  position: relative;
}
#mobile ul li a::after {
  position: absolute;
  content: "";
  bottom: -1px;
  left: 0;
  width: 0;
  height: 3px;
  border-radius: 1px;
  background-color: #F5FF00;
  transition: width 0.3s 0s;
}
#mobile ul li.on a::after {
  width: 100%;
}

#tab {
  position: relative;
  display: none;
  width: 30px;
  height: 24px;
}
#tab.on span {
  background-color: rgba(0, 0, 0, 0);
  transition-delay: 0.2s;
}
#tab.on span::before {
  background-color: #F5FF00;
  margin-top: 0;
  transform: rotate(45deg);
  transition-delay: 0s, 0.2s;
}
#tab.on span::after {
  background-color: #F5FF00;
  margin-top: 0;
  transform: rotate(-45deg);
  transition-delay: 0s, 0.2s;
}
#tab span {
  margin: 0 auto;
  position: relative;
  width: 100%;
  height: 2px;
  background-color: #fff;
  display: block;
  transition-duration: 0s;
  transition-delay: 0.2s;
  top: 12px;
}
#tab span::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  display: block;
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0s;
  margin-top: -8px;
}
#tab span::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  display: block;
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0s;
  margin-top: 8px;
}

.dim {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.3333333333);
  z-index: -1;
}
.dim.active {
  display: block;
}

@media only screen and (max-width: 940px) {
  #main {
    font-size: 0.9125em;
  }
  #main header h1 a {
