.swiper.sec02_swiper {
  width: 100%;
  height: 100%;
  overflow: visible;
}

#section02 {
  overflow-x: hidden;
  background-color: #252525;
}
#section02 .desc p.subtitle {
  color: #fff;
}
#section02 .swiper-wrapper {
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  height: 780px;
  gap: 15px;
  padding-top: 60px;
}
#section02 .swiper-slide {
  width: auto;
  height: auto;
  position: relative;
}
#section02 .swiper-slide:nth-child(2n) a p {
  right: -20px;
}
#section02 .swiper-slide:nth-child(2n+1) a p {
  left: -20px;
}
#section02 .swiper-slide:nth-child(3n)::before {
  position: absolute;
  content: "";
  top: 30px;
  width: 5px;
  height: 180px;
  background-color: #333;
  left: 4px;
}
#section02 .swiper-slide:nth-child(3n)::after {
  position: absolute;
  content: "";
  top: 30px;
  width: 5px;
  height: 180px;
  background-color: #333;
  right: 4px;
}
#section02 .swiper-slide:nth-child(3n) a::before {
  position: absolute;
  content: "";
  height: 2px;
  width: 130px;
  background-color: #333;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#section02 .swiper-slide:nth-child(4) {
  margin-top: 70px;
}
#section02 .swiper-slide:nth-child(9) {
  margin-top: 120px;
}
#section02 .swiper-slide:hover a {
  background-color: #F5FF00;
}
#section02 .swiper-slide:hover a::after {
  opacity: 1;
}
#section02 .swiper-slide:hover a p {
  display: block;
}
#section02 .swiper-slide:hover a span {
  font-weight: 700;
}
#section02 .swiper-slide a {
  display: block;
  width: 180px;
  height: 250px;
  font-size: 0.875em;
  position: relative;
  transition: 0.2s;
  background-color: #fff;
  z-index: -1;
  overflow: hidden;
}
#section02 .swiper-slide a::after {
  position: absolute;
  content: "";
  top: 5px;
  right: 5px;
  width: 14px;
  height: 14px;
  background: url(./images/arrow.png);
  background-size: cover;
  opacity: 0;
  transition: 0.3s;
}
#section02 .swiper-slide a span {
  position: absolute;
  bottom: 5px;
  left: 5px;
}
#section02 .swiper-slide a p {
  display: none;
  position: absolute;
  top: -20px;
  font-size: 20em;
  font-weight: 200;
  color: rgba(0, 0, 0, 0.1);
}

@media only screen and (max-width: 940px) {
  #section02 .swiper-slide a {
    width: 160px;
    height: 240px;
  }
}
@media only screen and (max-width: 720px) {
  #section02 .swiper-wrapper {
    flex-direction: row;
    flex-wrap: nowrap;
    height: auto;
  }
  #section02 .swiper-slide:nth-child(4), #section02 .swiper-slide:nth-child(9) {
    margin-top: 0;
  }
  #section02 .swiper-slide:nth-child(3n)::before, #section02 .swiper-slide:nth-child(3n)::after {
    height: 150px;
  }
  #section02 .swiper-slide a span {
    bottom: 0;
    top: 200px;
  }
}
